  z-index: 1;
}

.p-drawer__inner::after {
  display: block;
  position: absolute;
  z-index: 1;
  content: " ";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: calc(100% + 1px);
  z-index: -1;
}

.p-drawer.js-open {
