@use "sass:math";

////////////////////
// インナー幅設定
////////////////////
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キャンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 12px;
// ※PC時：デザインカンプの余白の幅ではない。PC時は通常25pxで大丈夫！

////////////////////
// フォント設定
////////////////////

$base-ff: "Noto Sans JP";
$en-ff: "oswald";

// ※検証ツールでしっかり反映されているか確認する

// フォントウェイト
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #333;
$white: #ffffff;
$red: #d40732;
$blue: #0a077e;
$darkgray: #777;
$gray: #aaa;

////////////////////
// メディアクエリとブレークポイントの設定
////////////////////

//ブレイクポイント
$breakpoints: (
	sm: 600,
	md: 768,
	lg: 1080,
	xl: 1440,
);

//メディアクエリー
$mediaquerys: (
	sm: "screen and (max-width: #{map-get($breakpoints,'sm')}px)",
	md: "screen and (max-width: #{map-get($breakpoints,'md')}px)",
	lg: "screen and (max-width: #{map-get($breakpoints,'lg')}px)",
	xl: "screen and (max-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}で書くとブレークポイントが反映される（初期値はmd）
@mixin mq($mediaquery: md) {
	@media #{map-get($mediaquerys, $mediaquery)} {
		@content;
	}
}

// remへの計算式（16pxを基準としている。10pxを基準とする場合は16を10に変更する）
@function rem($pixels) {
	@return math.div($pixels, 16) * 1rem;
}

body {
	font-family: $en-ff, $base-ff, sans-serif;

	padding-top: rem(100);
	@include mq("md") {
		border-right: 6px solid $black;
		border-left: 6px solid $black;
		border-bottom: 6px solid $black;
		padding-top: rem(60);
	}
}
////////////////////
// 非表示設定
////////////////////

//モバイルのみ表示
.u-mobile {
	display: none;
	@include mq("md") {
		display: block;
	}
}
//768px以上を表示
.u-desktop {
	@include mq("md") {
		display: none;
	}
}
